'use client'
import { ReactNode, useState } from 'react';
import { StaticImageData } from 'next/image';
import Image from 'next/image';
import BackgroundOverlay from './BackgroundOverlay';
import ScrollIndicator from './ScrollIndicator';

interface HomePageLayoutProps {
  children: ReactNode;
  backgroundImage: StaticImageData;
}

export default function HomePageLayout({ children, backgroundImage }: HomePageLayoutProps) {
  const [imageLoaded, setImageLoaded] = useState(false);
  
  return (
    <section className="min-h-screen relative overflow-hidden w-[80%] mx-auto">
      {/* Optimized Background Image with Next.js Image */}
      <div className="absolute inset-0">
        <Image
          src={backgroundImage}
          alt="Background"
          fill
          priority
          quality={75}
          placeholder="blur"
          sizes="(max-width: 768px) 100vw, 80vw"
          className="object-cover"
          onLoad={() => setImageLoaded(true)}
        />
      </div>
      
      {/* Gradient fallback while loading */}
      {!imageLoaded && (
        <div className="absolute inset-0 bg-gradient-to-br from-emerald-50 via-teal-100 to-green-200 animate-pulse" />
      )}
      
      <BackgroundOverlay />
      
      {/* Top Navigation Spacer */}
      <div className="h-16"></div>
      
      {/* Content Container */}
      <div className="relative z-10 h-full flex flex-col">
        {children}
      </div>
      
      <ScrollIndicator />
    </section>
  );
}